<template>
  <div class="add-property pb-2">
    <div>
      <div class="px-3 back_close d-flex justify-content-between align-items-center py-3 d-md-none">
        <b-icon font-scale="1.5" icon="arrow-left" @click="$router.go(-1)"></b-icon>
        <b-icon icon="x-lg" @click="$router.push(localePath('/'))"></b-icon>
      </div>
      <div class="post-form-container">
        <!-- <ProgressCircle :current-step="3" :total-steps="5"></ProgressCircle> -->
        <b-progress class="w-100 mt-lg-2" :value="3" :max="5" :height="progressHeight"></b-progress>
        <div class="p-3 step">
          <StayingDetails @nextStep="goNext" />
        </div>
      </div>
    </div>
    <WarningModal
      :show="showWarningModal"
      :routeTo="routeTo"
      @closeModal="showWarningModal = false"
      @closeModalAndNavigate="closeModalAndNavigate" />
  </div>
</template>

<script>
import StayingDetails from '@/components/postingRequestForm/StayingDetails';
import ProgressCircle from '@/components/postingRequestForm/ProgressCircle';
import WarningModal from '@/components/postingRequestForm/WarningModal';
export default {
  transition: {
    name: 'slide-fade',
    mode: 'out-in',
  },
  components: {
    StayingDetails,
    ProgressCircle,
    WarningModal,
  },
  data() {
    return {
      currentRequest: null,
      showWarningModal: false,
      routeTo: '',
      navigate: false,
    };
  },
  computed:{
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    appEnv(){
      return this.$config.APP_ENV
    },
    progressHeight(){
      if(process.client){
        if(window.innerWidth <= 991){
          return '3px'
        }else{
          return '5px'
        }
      }
    }
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "Posting Request | Step 2" : "نشر طلب | الخطوة 2"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? 'Posting Request | Staying Details' : 'نشر طلب | تفاصيل الاقامة'
        }
      ]
    }
  },
  methods: {
    goNext(values) {
      localStorage.setItem(
        'request',
        JSON.stringify({
          ...JSON.parse(this.currentRequest),
          stayingDetails: values.stayingDetails,
        })
      );
      this.$router.push(this.localePath('/requests/post/terms'));
    },
    closeModalAndNavigate() {
      this.showWarningModal = false;
      this.navigate = true;
    },
  },
  mounted() {
    this.currentRequest = localStorage.getItem('request');
  },
  beforeRouteLeave(to, from, next) {
    if (!to.path.includes('/requests/post/') && this.currentRequest) {
      this.showWarningModal = true;
      this.routeTo = to.path;

      if (this.navigate) return next();
    } else next();
  },
};
</script>

<style lang="scss">
.back_close {
  background-color: #f2f2f2 !important;
}
</style>