<template>
  <main class="second-step">
    <div class="container--fluid">
      <b-row class="flex-column">
        <b-col cols="12" class="step-heading">
          <h1 class="second-step__title font-weight-semi font-size-large text-center mb-4">
            {{ $t('form.postingRequest.stayingDetails.heading') }}
          </h1>
        </b-col>
        <!-- Night or Monthes or Year -->
        <b-col v-if="selectedTag" cols="12" lg="6" class="pt-2 mx-auto d-flex justify-content-center makeGap my-4">
          <button v-for="(duration, index) in selectedTag.price_type" :key="index" :class="['duration-type', 'lighter-gray', 'font-size-normal', 'mb-2', 'mt-2', dir === 'rtl' ? 'mr-1' : 'ml-1', stayingDetails.selectedDurationType === duration ? 'primary' : '']" @click="setDurationType(duration)" type="button">
            <div class="circle-container">
              <div class="inner-circle"></div>
            </div>
            <span>{{ formating_price_type(duration) }}</span>
          </button>
        </b-col>

        <b-col cols="12" lg="6" class="my-3 step-content pt-2 mx-auto">
          <div class="section-white px-minus-15">
            <b-row cols="12" class="px-2 my-2 align-items-center justify-content-between">
              <b-col cols="6">
                <h1 class="font-size-normal font-weight-bold text-dark">
                  {{ dir === 'ltr' ? `Number of ${formating_price_type(stayingDetails.selectedDurationType)}` : `عدد ال${formating_price_type(stayingDetails.selectedDurationType)}` }}
                  <span style="color: red">*</span>
                </h1>
              </b-col>
              <b-col cols="6" class="d-flex justify-content-end">
                <CustomCounter
                  id="duration"
                  :step="1"
                  validation-rules="required|min_value:1"
                  v-model="stayingDetails.selectedDuration"
                  :min="1"
                  :max="selectedDurationNumber" />
              </b-col>
            </b-row>
            <hr>
            <b-row cols="12" class="px-2 my-2 align-items-center justify-content-between">
              <b-col cols="5">
                <h1 class="font-size-normal font-weight-bold text-dark">
                  {{ date_StartingDate }}
                  <span style="color: red">*</span>
                </h1>
              </b-col>
              <b-col cols="7" class="d-flex justify-content-end">
                <b-form-datepicker
                  class="pr-4"
                  :date-format-options="dateFormatOptions"
                  id="datepicker"
                  v-model="stayingDetails.startingDate"
                  @input="changeDate"
                  :placeholder="$t('common.date')"
                  today-button
                  reset-button
                  close-button
                  locale="en"
                  :min="new Date().toJSON().slice(0, 10).replace(/-/g, '-')"
                  aria-describedby="inputcheck-in-feedback"
                  :state="startingDateState" />
                <b-form-invalid-feedback id="input-live-feedback">
                  Enter a valid starting date
                </b-form-invalid-feedback>
              </b-col>
            </b-row>
          </div>
        </b-col>
        <b-col cols="12" lg="6" class="my-3 step-content pt-2 mx-auto">
          <b-row>
            <b-col cols="12" class="mt-4 section-white">
              <b-row class="align-items-center mb-4">
                <b-col md="12" lg="6">
                  <h5 :class="['font-size-normal font-weight-bold text-dark', 'mb-2', dir === 'rtl' ? 'mr-1' : 'ml-1']">
                    {{ $t('form.postingRequest.stayingDetails.budget_range') }}
                    <span style="color: red">*</span>
                    <span class="font-size-smaller light-gray" v-if="stayingDetails.selectedDuration">
                      ({{ formating_price_type_ly() }})
                    </span>
                  </h5>
                </b-col>
                <b-col md="12" lg="6">
                  <b-form-input
                    v-model="stayingDetails.budgetRange"
                    :placeholder="
                      $t(
                        'form.postingRequest.stayingDetails.budget_placeHolder'
                      )
                    "
                    type="number"
                    :state="budgetState"></b-form-input>
                  <b-form-invalid-feedback id="input-live-feedback">
                    Budget range must be over 99 EGP
                  </b-form-invalid-feedback>
                </b-col>
              </b-row>
            </b-col>
            <b-col cols="12" class="mt-4 section-white">
              <b-row class="align-items-center mt-4">
                <b-col md="12" lg="6">
                  <h5 :class="['font-size-normal font-weight-bold text-dark', 'mb-2', dir === 'rtl' ? 'mr-1' : 'ml-1']">
                    {{ $t('form.postingRequest.stayingDetails.average_Price') }}
                  </h5>
                </b-col>
                <b-col md="12" lg="6">
                  <p v-if="dir === 'ltr'" class="myParagraph">The average rate for this area is between <span class="font-weight-bold">220 LE</span> to <span class="font-weight-bold">250 LE</span> per month or <span class="font-weight-bold">30 LE</span> to <span class="font-weight-bold">35 LE</span> per day.</p>
                  <p v-else class="myParagraph">متوسط السعر فى هذه المنطقة ما بين <span class="font-weight-bold">220 جنية</span> إلى <span class="font-weight-bold">250 جنية</span> فى الشهر أو <span class="font-weight-bold">30 جنية</span> إلى <span class="font-weight-bold">35 جنية</span></p>
                </b-col>
              </b-row>
            </b-col>
          </b-row>
          <b-row class="mt-4" v-if="false">
            <b-col cols="8" class="mt-4 section-white mx-auto">
              <!-- v-if="barChartData && barChartData.labels && barChartData.labels.length" -->
              <BarsChart />
            </b-col>
          </b-row>
        </b-col>

        <b-col cols="12" class="px-0">
          <b-row class="step-next-container mx-auto">
            <b-col cols="5">
              <div>
                <b-button
                  @click="
                    () => {
                      $router.push(this.localePath('/requests/post/destination'));
                    }
                  "
                  variant="primary"
                  class="back-btn">
                  <span>{{ $t('common.back') }}</span>
                </b-button>
              </div>
            </b-col>
            <b-col cols="7">
              <div>
                <b-button
                  @click="handleNextClick"
                  variant="primary"
                  class="next-btn"
                  :disabled="!budgetState || !startingDateState">
                  <span>{{ $t('common.next') }}</span>
                </b-button>
              </div>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </div>
  </main>
</template>

<script>
import changeCurrent from '@/mixins/changeCurrent';
import calculateMoveOutData from '@/mixins/calculateMoveOutData';

export default {
  name: 'StayingDetails',
  mixins: [changeCurrent, calculateMoveOutData],

  components: {
    ChipSelector: () => import('~/components/shared/form/chipSelector'),
    CustomCounter: () => import('~/components/shared/form/CustomCounter'),
    BarsChart: () => import('~/components/postingRequestForm/BarsChart'),
  },
 
  data() {
    return {
      days: 31,
      months: 12,
      years: 10,
      moveOutDate: null,
      selectedTag: null,
      dateFormatOptions: {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
      },
      stayingDetails: {
        selectedDurationType: '',
        selectedDuration: 1,
        startingDate: new Date().toISOString().split('T')[0],
        budgetRange: '',
        move_out_date: '',
      },
    };
  },
  computed: {
    selectedDurationNumber() {
      return this.stayingDetails ? this[this.stayingDetails.selectedDurationType] : 0;
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    duration() {
      return this.$dateFns.differenceInDays(
        new Date(this.moveOutDate),
        new Date(this.stayingDetails.startingDate)
      );
    },
    budgetTerm() {
      return this.formating_price_type_ly();
    },
    budgetState() {
      if (this.stayingDetails.budgetRange === '') return null;
      return this.stayingDetails.budgetRange >= 100;
    },
    startingDateState() {
      if (this.stayingDetails.startingDate === '') return null;
      return !!this.stayingDetails.startingDate;
    },
    selectedIntialDurationType(){
      this.selectedDurationType = this.selectedDurationType; 
      this.selectedDurationType = this.dir === 'ltr' ?  this.selectedDurationType : 'أيام';
      return this.selectedDurationType
    },
    date_StartingDate(){
      let ifHourly = false;
      if(!ifHourly){
        return this.$t('form.postingRequest.stayingDetails.starting_date')
      }else{
        return this.dir === 'ltr' ? 'Date' : 'التاريخ';
      }
    },
  },

  watch: {
    stayingDetails: {
      handler(newValue, oldValue) {
        const { selectedDurationType, selectedDuration, startingDate } =
          this.stayingDetails;
        this.calculateMoveOutData({
          durationType: selectedDurationType,
          duration: selectedDuration,
          startingDate,
        });
      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
    calculateMoveOutData(){
      switch (this.stayingDetails.selectedDurationType){
        case 'dy':
          this.stayingDetails.move_out_date = this.$dateFns.format(new Date(this.$dateFns.add(new Date(this.stayingDetails.startingDate), {days: this.stayingDetails.selectedDuration})), 'yyyy-MM-dd');
          break;
        case 'my':
          this.stayingDetails.move_out_date = this.$dateFns.format(new Date(this.$dateFns.add(new Date(this.stayingDetails.startingDate), {months: this.stayingDetails.selectedDuration})), 'yyyy-MM-dd');
          break;
        case 'yy':
          this.stayingDetails.move_out_date = this.$dateFns.format(new Date(this.$dateFns.add(new Date(this.stayingDetails.startingDate), {years: this.stayingDetails.selectedDuration})), 'yyyy-MM-dd');
          break;
        case 'hr':
          this.stayingDetails.move_out_date = this.$dateFns.format(new Date(this.$dateFns.add(new Date(this.stayingDetails.startingDate), {hours: this.stayingDetails.selectedDuration})), 'yyyy-MM-dd');
          break;
        case 'wy':
          this.stayingDetails.move_out_date = this.$dateFns.format(new Date(this.$dateFns.add(new Date(this.stayingDetails.startingDate), {weeks: this.stayingDetails.selectedDuration})), 'yyyy-MM-dd');
          break;
        default:
          this.stayingDetails.move_out_date = this.$dateFns.format(new Date(this.$dateFns.add(new Date(this.stayingDetails.startingDate), {days: 1})), 'yyyy-MM-dd')
      }
    },
    changeDate(value) {
      this.stayingDetails.startingDate = value;
      this.calculateMoveOutData();
    },
    setDurationType(duration) {
      this.stayingDetails.selectedDurationType = duration;
      this.calculateMoveOutData();
    },
    handleDurationNumberClick(value) {
      this.stayingDetails.selectedDuration = value;
    },
    handleNextClick() {
      let {
        budgetRange,
        startingDate,
        selectedDuration,
        selectedDurationType,
      } = this.stayingDetails;


      if (
        !budgetRange ||
        !startingDate ||
        !selectedDuration ||
        !selectedDurationType
      ) {
        let body = this.$t('form.checkListFirst');
        this.$bvToast.toast(body, {
          title: this.$t('form.fillInput'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 1000,
        });
        return;
      }

      this.$emit('nextStep', {
        stayingDetails: {
          ...this.stayingDetails,
          ...{ move_out_date: this.moveOutDate },
        },
        term: this.duration > 30 ? 1 : 0,
      });
        const request = localStorage.getItem('request') ? JSON.parse(localStorage.getItem('request')) : null;
        localStorage.setItem('request', JSON.stringify({...request, stayingDetails: this.stayingDetails}))
    },
    formating_price_type(item){
      switch (item){
        case 'dy':
          return this.$t('form.postingRequest.stayingDetails.days')
        case 'my':
          return this.$t('form.postingRequest.stayingDetails.months')
        case 'yy':
          return this.$t('form.postingRequest.stayingDetails.years')
        case 'hr':
          return this.$t('form.postingRequest.stayingDetails.hours')
        case 'wy':
          return this.$t('form.postingRequest.stayingDetails.weeks')
        default:
          return false  
      }
    },
    formating_price_type_ly(){
      switch (this.stayingDetails.selectedDurationType){
        case 'dy':
          return this.$t('common.daily')

        case 'my':
          return this.$t('common.monthly')

        case 'yy':
          return this.$t('common.yearly')
          
        case 'hr':
          return this.$t('common.hourly')

        case 'wy':
          return this.$t('common.weekly')

        default:
          return false  
      }
    },
  },
  mounted() {
    const request = localStorage.getItem('request') ? JSON.parse(localStorage.getItem('request')) : this.$router.push(this.localePath('/requests/post/property-category'));

    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });
    
    if(!localStorage.getItem('request')){ // Watch The LocalStorage Request
      this.$router.push('/requests/post/property-category');
    }

    // const currentStayingDetails = localStorage.getItem('request') ? JSON.parse(localStorage.getItem('request')).stayingDetails : this.$router.push('/requests/post/destination');
    
    // if (!!currentStayingDetails.selectedDurationType)
    //   this.stayingDetails = currentStayingDetails;
  },
  created(){
    if(process.client){
      const request = localStorage.getItem('request') ? JSON.parse(localStorage.getItem('request')) : this.$router.push(this.localePath('/requests/post/property-category'));
      
      if(request.propertyCategory){
        this.selectedTag = request.propertyCategory;
        this.stayingDetails.selectedDurationType = this.selectedTag.price_type[0];
        if(!!request.stayingDetails){
          this.stayingDetails = request.stayingDetails;
        }
      }else{
        this.$router.push(this.localePath('/requests/post/property-category'))
      }
    }
  }
};
</script>

<style scoped lang="scss">
.myParagraph{
  font-size: 11px;
}
.makeGap {
  gap: 20px;
}
.second-step {
  min-height: 100vh;
  .step-content .b-form-datepicker .form-control[for='datepicker'] {
    padding-right: 0 !important;
  }
}
.duration-type {
  background-color: transparent;
  border: 1px solid #111111;
  border-radius: 8px;
  font-weight: 600;
  padding: 7px 12px;
  display: flex;
  gap: 4px;
  span {
    font-weight: 600;
    font-size: 12px;
    line-height: 12px;
    color: #111111;
    margin: auto;
  }
  .circle-container {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
  &.primary {
    .inner-circle {
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background-color: $primary;
      margin: auto;
    }
  }
}
.duration-indicator {
  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
  color: #111111;
}
.inner {
  display: flex;
  justify-content: space-between;
  overflow: scroll;
  padding-bottom: 20px;
  gap: 24px;
  span {
    width: 38px;
    height: 38px;
    padding: 10px 12px;
    font-weight: 700;
    font-size: 12px;
    text-align: center;
    border-radius: 50%;
    border: 1px solid #e3e5e5;
    color: #48535b;
    cursor: pointer;
    &.active {
      color: #0877f1;
      border-color: #0877f1;
    }
  }
}

.px-minus-15 {
  margin-left: -15px !important;
  margin-right: -15px !important;
}
</style>