export default {
  methods: {
    calculateMoveOutData({ durationType, duration, startingDate }) {
      if (durationType === 'days' || durationType === 'أيام') {
        this.moveOutDate = this.$dateFns.addDays(
          new Date(startingDate),
          duration
        )
      }

      if (durationType === 'months' || durationType === 'شهور') {
        this.moveOutDate = this.$dateFns.addMonths(
          new Date(startingDate),
          duration
        )
      }

      if (durationType === 'years' || durationType === 'سنين') {
        this.moveOutDate = this.$dateFns.addYears(
          new Date(startingDate),
          duration
        )
      }
    },
  },
}